<template lang="html">
  <section class="lottery-users">
    <!--  -->
    <div class="">
      <span></span>
      <el-date-picker
        v-model="value5"
        @change="handleDatePickerChange"
        type="datetimerange"
        :picker-options="pickerOptions2"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        size="small"
        align="right"
      />
    </div>
    <!--  -->
    <div class="table-list">
      <!--  -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" />

        <el-table-column prop="username" label="" />

        <el-table-column prop="nickname" label="" />

        <el-table-column label="" :min-width="120">
          <template slot-scope="scope">
            <span>{{scope.row.banlance | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="rechargeAmount" label="" />

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.rechargeMoney | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="withdrawAmount" label="" />

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.withdrawMoney | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.backendAddMoney | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.backendSubtractMoney | RMB}}</span>
          </template>
        </el-table-column>

        <!-- <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="showTip"  size="mini"></el-button>
          </template>
        </el-table-column> -->
      </el-table>
      <!--  -->
      <BasePagination
        @on-change="handlePaginationChange"
        :pageTotal="pageTotal"
        :requestParams="requestParams"
        httpURL="fetchFinanceSheet"
      />
    </div>
  </section>
</template>

<script>
import FilterArea from '@/components/others/FilterArea'
import BasePagination from '@/components/base/BasePagination'

export default {
  name: 'LotteryUsersInfo',
  components: {
    FilterArea,
    BasePagination
  },
  data () {
    return {
      tableData: [],
      pageTotal: 0,
      requestParams: {},
      value5: '',
      pickerOptions2: {
        shortcuts: [{
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      }
    }
  },
  created () {
    this.fetchFinanceSheet()
  },
  methods: {
    handleDatePickerChange (timeArray) {
      console.log(Date.parse(timeArray[0]))
      console.log(Date.parse(timeArray[1]))
      this.requestParams = {
        startInTime: String(Date.parse(timeArray[0])),
        endInTime: String(Date.parse(timeArray[1]))
      }
      this.fetchFinanceSheet()
    },
    showTip () {
      this.$message.warning('...')
    },
    // 
    handlePaginationChange (data) {
      this.tableData = data
    },
    fetchFinanceSheet () {
      this.$httpAPI.fetchFinanceSheet({
        params: Object.assign({ pageNo: 1, pageSize: 10 }, this.requestParams)
      }).then(response => {
        this.tableData = response.data.data
        this.pageTotal = response.data.amount
      }).catch(error => console.log(error))
    }
  }
}
</script>
